<template>
  <div class="home">
    <div ref='Area'>
      <transition name="el-zoom-in-center">
        <div class="sign-all"  v-show="showSign">
          <div class="sign"></div>
          <div class="sign-x"></div>
        </div>
      </transition>
      <el-collapse-transition>
        <div class="card" v-show="showCard">
          <p class="content">“我们已经在一起”</p>
          <p class="content">“{{ day }} 天”</p>
          <p class="content">“{{ hour }} 小时”</p>
          <p class="content">“{{ minute }} 分”</p>
          <p class="content">“{{ second }} 秒”</p>
          <p class="content">“{{ millisecond }} 豪秒”</p>
        </div>
      </el-collapse-transition>
    </div>
    </div>
</template>

<script>
import { onDeactivated, onMounted, ref } from "vue";
import * as THREE from 'three'
import BIRDS from 'vanta/src/vanta.birds'

export default {
  name: 'HomeView',
  components: {
  },
  setup: function () {
    const showCard = ref(false);
    const showSign = ref(false);
    // 定时器
    const timer = ref(0);
    // 毫秒
    const millisecond = ref(0);
    // 秒
    const second = ref(0);
    // 分
    const minute = ref(0);
    // 时
    const hour = ref(0);
    // 天
    const day = ref(0);
    // 使用ref引用挂载区域
    const Area = ref(null);
    //创建一个全局的变量来使用vanta.js
    /**
     *因为在vue2中，是使用this.vantaEffect来创建指定的3d动画模板的
     *但是vue3 setup中是没有this，所以要另外创建一个
     **/
    let vantaEffect=null;
    //组件挂载时的生命周期执行的方法
    onMounted(()=>{
      timer.value = window.setInterval(function getTime() {
        //getTime() 返回从 1970 年 1 月 1 日至今的毫秒数。
        //1秒=1000毫秒  3600=1小时*1分钟   24=24小时
        // 获取中间时间戳
        const number = new Date().getTime()-new Date("2022/09/22 00:28:12").getTime();
        // 毫秒
        millisecond.value = number;
        // 秒
        second.value = parseInt((number / 1000).toString());
        // 分
        minute.value = parseInt((number / (1000 * 60)).toString());
        // 时
        hour.value = parseInt((number / (1000 * 60 * 60)).toString());
        // 天
        day.value = parseInt((number / (1000 * 60 * 60 * 24)).toString());
      }, 1);

      window.setTimeout(function transition() {
        showCard.value = true;
      }, 1500);

      window.setTimeout(function transition() {
        showSign.value = true;
      }, 1000);

      vantaEffect=BIRDS({
        el:Area.value,
        THREE:THREE,
        // 如果需要改变样式，要写在这里
        // 因为这里vantaEffect是没有setOptions这个方法的
        mouseControls: true,
        touchControls: true,
        minHeight: document.body.clientHeight,
        minWidth: document.body.clientWidth,
        scale: 1.00,
        scaleMobile: 1.00,
        backgroundColor: 0xffffff,
        color1: 0x9a2626,
        colorMode: "lerp",
        birdSize: 1.50,
        wingSpan: 20.00,
        speedLimit: 6.00,
        separation: 50.00,
        alignment: 39.00,
        cohesion: 29.00,
        quantity: 4.00,
        backgroundAlpha: 0.00
      })
    })

    // 离开当前组件的生命周期执行的方法
    onDeactivated(()=>{
      window.clearInterval(timer.value);
      if(vantaEffect){
        vantaEffect.destroy()
      }
    })
    // 暴露 state 到模板
    return {
      second,
      minute,
      hour,
      day,
      millisecond,
      showCard,
      showSign,
      Area
    }
  }
}
</script>

